.summary-content {
  &__wrapper {
    justify-content: center;
  }
}

.contact-links {
  &__container {
    text-align: center;
    margin: 40px;
    padding: 40px;
    display: flex;
    justify-content: center;

    .link {
        opacity: 0;
        transition: opacity 0.5s ease-in-out;
        margin: 0 20px;

        &:hover {
            text-decoration: underline;
        }
        .fa {
            margin-bottom: 8px;
        }
    }
    .show-link {
        opacity: 1;
    }
  }
}

.copyright {
    text-align: center;
    padding: 40px;
}
.attribution {
    font-size: 14px;
    padding: 0 20px 40px;
    max-width: 515px;
    margin: 0 auto;
}
